@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:5px}body{font-family:montserrat, arial, verdana}span{font-weight:bold}h3{text-decoration:underline}.search__container{text-align:center}.search__departure-arrival{display:flex;justify-content:space-evenly}@media screen and (max-width: 800px){.search__departure-arrival,.search__status{display:inline-block}}

